import NcAppSidebarTab from '@nextcloud/vue/components/NcAppSidebarTab';
import NcEmptyContent from '@nextcloud/vue/components/NcEmptyContent';
export default {
  name: 'SidebarTab',
  components: {
    NcAppSidebarTab,
    NcEmptyContent
  },
  props: {
    fileInfo: {
      type: Object,
      default: () => {},
      required: true
    },
    id: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: true
    },
    icon: {
      type: String,
      default: ''
    },
    /**
     * Lifecycle methods.
     * They are prefixed with `on` to avoid conflict with Vue
     * methods like this.destroy
     */
    onMount: {
      type: Function,
      required: true
    },
    onUpdate: {
      type: Function,
      required: true
    },
    onDestroy: {
      type: Function,
      required: true
    },
    onScrollBottomReached: {
      type: Function,
      default: () => {}
    }
  },
  data() {
    return {
      loading: true
    };
  },
  computed: {
    // TODO: implement a better way to force pass a prop from Sidebar
    activeTab() {
      return this.$parent.activeTab;
    }
  },
  watch: {
    async fileInfo(newFile, oldFile) {
      // Update fileInfo on change
      if (newFile.id !== oldFile.id) {
        this.loading = true;
        await this.onUpdate(this.fileInfo);
        this.loading = false;
      }
    }
  },
  async mounted() {
    this.loading = true;
    // Mount the tab:  mounting point,   fileInfo,      vue context
    await this.onMount(this.$refs.mount, this.fileInfo, this.$refs.tab);
    this.loading = false;
  },
  async beforeDestroy() {
    // unmount the tab
    await this.onDestroy();
  }
};